import React from "react";
import Player from "../../../../commons/player/Player";

function Attributions({ attributions, passed }) {
  return (
    <>
      {attributions.map((attrib, index) => {
        if (attrib.list) {
          return (
            <span
              style={{
                marginLeft: index !== 0 ? 4 : 0,
                display: passed ? "inline" : "block",
              }}
            >
              {!passed && "["}
              {attrib.list.map((attribList, i) => (
                <span
                  key={attribList.player}
                  style={{
                    marginLeft: i !== 0 ? 4 : 0,
                  }}
                >
                  <Player
                    name={attribList.player}
                    classWoW={attribList.class}
                  />
                </span>
              ))}
              {!passed && "]"}
            </span>
          );
        } else {
          return (
            <span
              key={attrib.player}
              style={{
                marginLeft: index !== 0 ? 4 : 0,
                display: passed ? "inline" : "block",
              }}
            >
              <Player name={attrib.player} classWoW={attrib.class} />
            </span>
          );
        }
      })}
    </>
  );
}

export default Attributions;
