export default function getWoWClassColor(classWoW) {
  let color;
  switch (classWoW) {
    case "Druide":
      color = "#FF7D0A";
      break;
    case "Chasseur":
      color = "#ABD473";
      break;
    case "Mage":
      color = "#69CCF0";
      break;
    case "Paladin":
      color = "#F58CBA";
      break;
    case "Prêtre":
      color = "#FFFFFF";
      break;
    case "Voleur":
      color = "#FFF569";
      break;
    case "Chaman":
      color = "#0070DE";
      break;
    case "Guerrier":
      color = "#C79C6E";
      break;
    case "Démoniste":
      color = "#9482C9";
      break;
  }
  return color;
}
