import React from "react";
import { Table } from "react-bootstrap";
import Attributions from "./attributions/Attributions";
import getPriorityLoot from "../../../utils/getPriorityFromLoot";

function LootTable({ loots }) {
  return (
    <Table bordered hover responsive variant="dark">
      <thead>
        <tr>
          <th>Loot</th>
          <th>Prochaines Attributions</th>
          <th>Reçu</th>
        </tr>
      </thead>
      <tbody>
        {loots.sort().map((loot) => {
          const priorities = getPriorityLoot(loot);
          const alreadyAcquired = priorities.filter((p) =>
            p.list ? p.priority === 0 : p.acquired
          );
          const attributions = priorities.filter((p) =>
            p.list ? p.priority !== 0 : !p.acquired
          );
          return (
            <tr key={loot}>
              <td>{loot}</td>
              <td style={{ maxWidth: 300 }}>
                <Attributions attributions={attributions} />
              </td>
              <td style={{ maxWidth: 300 }}>
                <Attributions attributions={alreadyAcquired} passed />
              </td>
            </tr>
          );
        })}
      </tbody>
    </Table>
  );
}

export default LootTable;
