import React, { useState } from "react";
import players from "../data/players";
import { Accordion, Button, Table } from "react-bootstrap";
import Wishlist from "./wishlist/Wishlist";
import Player from "../commons/player/Player";
import getWoWClassColor from "../utils/getWoWClassColor";

function Players() {
  const [sort, setSort] = useState("Name");
  return (
    <Accordion>
      <Table variant="dark">
        <thead>
          <tr>
            <th style={{ cursor: "pointer" }} onClick={() => setSort("Name")}>
              Name
            </th>
            <th style={{ cursor: "pointer" }} onClick={() => setSort("Class")}>
              Class
            </th>
            <th style={{ cursor: "pointer" }} onClick={() => setSort("Spé")}>
              Spé
            </th>
            <th></th>
          </tr>
        </thead>
        <tbody>
          {players
            .sort((p1, p2) => {
              switch (sort) {
                case "Class":
                  return p1.class.localeCompare(p2.class);
                case "Spé":
                  return p1.spe.localeCompare(p2.spe);
                case "Name":
                default:
                  return p1.name.localeCompare(p2.name);
              }
            })
            .map((player) => {
              return (
                <>
                  <tr>
                    <td>
                      <Player name={player.name} classWoW={player.class} />
                    </td>
                    <td style={{ color: getWoWClassColor(player.class) }}>
                      {player.class}
                    </td>
                    <td>{player.spe}</td>
                    <td>
                      <Accordion.Toggle
                        as={Button}
                        variant="link"
                        eventKey={player.name}
                      >
                        wishlist
                      </Accordion.Toggle>
                    </td>
                  </tr>
                  <tr>
                    <td colspan="4" style={{ padding: 0 }}>
                      <Accordion.Collapse eventKey={player.name}>
                        <Wishlist wishlist={player.wishlist} />
                      </Accordion.Collapse>
                    </td>
                  </tr>
                </>
              );
            })}
        </tbody>
      </Table>
    </Accordion>
  );
}

export default Players;
